import React, { Component } from 'react';
import DataSharingContext from '../context/data-sharing-context';

class SearchComponent extends Component {
    onHandleChange=(Event)=>{
        console.log(Event.target.value)
        this.context.fromSearch(Event.target.value)
    }
    render() {
        return (
            <DataSharingContext.Consumer>
                {
                    (context) => {
                        console.log(context)
                        return (
                            <div>
                                <input type="text" className="search-box" placeholder="Enter a search value..." onChange={this.onHandleChange} />

                            </div>

                        )
                    }

                }

            </DataSharingContext.Consumer>
        );
    }
}
SearchComponent.contextType = DataSharingContext;

export default SearchComponent;