import React, { Component } from 'react';
import axios from 'axios';

class TrashPage extends Component {

    constructor(props){
        super(props);
        this.state={
            edit:{
                name:" ",
                job:" "
            }
        }

    }
    onHandleInput=(Event)=>{
        this.setState({
            edit:{...this.state.edit,[Event.target.name]:Event.target.value}
        })
    }
    onUpdate(){
        console.log(this.state.edit)

        const url="https://reqres.in/api/users/2"
        axios.put(url,this.state.edit)
           .then((response)=>{
               console.log(response)
           })
           .catch((error)=>{
               console.log(error)
           })


    }
    render() {
        return (
            <div>
                <h1>This is Trash Page</h1>
                <div>
                    <label>Edit UserName:</label>
                    <input type="text" placeholder="edit username.." name="name" onChange={this.onHandleInput}/>
                </div>
                <div>
                    <label>Edit Job:</label>
                    <input type="text" placeholder="edit job.." name="job" onChange={this.onHandleInput}/>
                </div>
                <button onClick={()=>{this.onUpdate()}}>UpdateInfo</button>
                
            </div>
        );
    }
}

export default TrashPage;