import React, { Component } from 'react';

class LifeCycle extends Component {
    constructor(props) {
        super(props);
        this.state={
            name:"sandhiya"
        }
        console.log("Called Constructor Method")
    }
    onHandleInput(){
        this.setState({
            name:"dhiya"
        })
    }
    shouldComponentUpdate(nextprops,nextstate){
        console.log(nextprops,nextstate)
        return true;
     }
        
    componentWillMount(){
        console.log("Called ComponentWillMount Method")
    }
    
    componentDidMount(){
            console.log("ComponentDidMount Method")
        }
    componentWillUnmount(){
        console.log("Called Component Will Unmount")
    }
    componentWillUpdate(){
        console.log("Called Component Will Update")
    }
    componentDidUpdate(){
        console.log("Called Component Did Update")
    }
    render() {
        console.log("Called Render Method")

        return (
            <div>
                <h1>"The user name is:"{this.state.name}</h1>
                <h1>This is Life Cycle Page</h1>
                <button onClick={()=>{this.onHandleInput()}}>Change</button>
            </div>
        );
    }
}


export default LifeCycle;